import React, { useState, useEffect } from 'react';
import axios, { AxiosError } from 'axios';
import './Home.scss';

const Home = () => {
  const [movies, setMovies] = useState([]);

  const getMovies = () => {
    axios.get('https://api.themoviedb.org/3/movie/now_playing', {
      params: {
        api_key: 'd21d6f9a11307550b8fe09b60f3ee8ef',
        language: 'fr-FR',
        iso_3166_1: 'FR',
        english_name: 'France',
      },
    })
    .then((res) => {
      setMovies(res.data.results);
    })
    .catch(() => (
      AxiosError
    ));
  };

  useEffect(() => {
    getMovies();
  }, []);

  return (
    <div className="home">
      {
        movies.filter((c) => c.title !== '' && c.poster_path !== null)
        .map(({ poster_path, title }) => (
          <div className="home-container-movie">
            <img className="home-poster" src={`https://image.tmdb.org/t/p/w500${poster_path}`} alt={title}/>
            <h2 className="home-titleMovie">{title}</h2>
          </div>
        ))
      }
    </div>
  );
}

export default Home;
